class ListaAnexosView extends View{
	constructor(elemento){
		super(elemento)
    }

    template(model){
       // console.log("anexosView", model);
        return `
        ${model.lista.map((anexo, index, array) =>`
            <ul>
                <li>
                    <a href="${anexo.link}" target="_blank">${anexo.nome}</a>
                    <a href="#" class="text-danger" onclick="cadastroController.removeAnexo(event, ${index});" ><i class="fa fa-minus-circle"></i></a>
                </li>
            </ul>
        `).join('')}
        `
    }
}